import {profileAPI, usersAPI} from "../api/api";

const ADD_POST = 'ADD_POST';
const DELETE_POST = 'DELETE_POST';
const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const SET_PHOTO = 'SET_PHOTO';

let initialState = {
    posts: [
        {id: 1, message: "It's my first post", like: 20},
        {id: 2, message: "Hello there!", like: 12},
    ],
    profile: null,
    status: ""
};

 const profileReducer = (state = initialState,action ) => {
    switch (action.type) {
        case ADD_POST: {
            let newPost = {
            id: 3,
            message: action.newPostText,
            like: 0
        };
        return  {
            ...state,
            posts: [...state.posts,  newPost],
            newPostText : '',
           };
        }
        case DELETE_POST: {
        return  {
            ...state,
            posts: state.posts.filter(p => p.id != action.postId)
              };
        }
        case SET_USER_PROFILE : {
            return  {
                ...state, profile:action.profile
            }
        }
        case SET_STATUS : {
            return  {
                ...state, status:action.status
            }
        }
        case SET_PHOTO : {
            return  {
                ...state, profile: {...state.profile, photos: action.photos}
            }
        }
        default:
            return state;
    }

}

export const addPostActionCreator = (newPostText) => {return {type: ADD_POST, newPostText}}
export const setUserProfile = (profile) => {return {type: SET_USER_PROFILE, profile}}
export const setStatus = (status) => {return {type: SET_STATUS, status}}
export const savePhotoSuccess = (photos) => {return {type: SET_PHOTO, photos}}

export const deletePost = (postId) => {return {type: DELETE_POST, postId}}

export const getUserProfile = (userId) => async (dispatch) => {
    let response = await usersAPI.getProfile(userId);
            dispatch (setUserProfile(response.data));
}

export const getStatus = (userId) => async (dispatch) => {
    let response = await profileAPI.getStatus(userId)
        dispatch (setStatus(response.data));
}

export const updateStatus = (status) => async (dispatch) => {
    let response = await profileAPI.updateStatus(status)
        if (response.data.resultCode === 0) {
            dispatch (setStatus(status));
        }
}
export const uploadPhoto = (image) => async (dispatch) => {
    let response = await profileAPI.uploadPhoto(image)
        if (response.data.resultCode === 0) {
            dispatch (savePhotoSuccess(response.data.data.photos));
        }
}
export const saveProfile = (profile,setFieldValue) => async (dispatch, getState) => {
     const userId= getState().auth.userId;
    let response = await profileAPI.saveProfile(profile)
        if (response.data.resultCode === 0) {
           dispatch (getUserProfile(userId));
        }else {
            setFieldValue("general", response.data.messages[0])
        }
}

export default profileReducer;