import {authAPI, securityAPI} from "../api/api";

const SET_USER_DATA = 'auth/SET_USER_DATA';
const GET_CAPTCHA_URL = 'auth/GET_CAPTCHA_URL';

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    // isFetching : false,
    captchaUrl: null

};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            //  debugger
            return {
                ...state,
                ...action.payload,
            }
            case GET_CAPTCHA_URL:
            //  debugger
            return {
                ...state,
                ...action.payload,
            }

        default:
            return state;
    }

}

export const setAuthUserData = (userId, email, login, isAuth) => ({
    type: SET_USER_DATA,
    payload: {userId, email, login, isAuth}
})
export const setCaptchaUrl = (captchaUrl) => ({
    type: GET_CAPTCHA_URL, payload: {captchaUrl}
})
//export const toggleIsFetching = (isFetching) => {return {type: TOGGLE_IS_FETCHING,isFetching }}

export const getAuthUserData = () => async (dispatch) => {
    let response = await authAPI.me();
    //debugger
    if (response.data.resultCode === 0) {
        let {id, email, login} = response.data.data;
        dispatch(setAuthUserData(id, email, login, true));
    }
}
export const login = (email, password, rememberMe,captcha, setFieldValue) => async (dispatch) => {
    let response = await authAPI.login(email, password, rememberMe,captcha,setFieldValue);
            //debugger
            if (response.data.resultCode === 0) {
                dispatch(getAuthUserData());
            } else {
                if (response.data.resultCode === 10) {
                    dispatch(getCaptchaUrl());
                }
                setFieldValue("general", response.data.messages.join(" "))
            }
}
export const logout = () =>  async (dispatch) => {
    let response = await authAPI.logout();
            if (response.data.resultCode === 0) {
                dispatch(setAuthUserData(null, null, null, false));
            }
}
export const getCaptchaUrl = () =>  async (dispatch) => {
    let response = await securityAPI.captcha();
      const captchaUrl = response.data.url;
      dispatch (setCaptchaUrl(captchaUrl));
}

export default authReducer;